import { Collapse,Select,Card,Layout, Table, Tag, Space,Popover, List, Button,Typography, Divider,Statistic, Affix } from 'antd';
import 'antd/dist/antd.css';
import { CaretRightOutlined } from '@ant-design/icons';
import './App.css';
import rawSkillList from './result_upd.json'
import wujiangList from './wujiangList.json'
import React from 'react';


const {Header, Content, Footer} =Layout;
const {Option}=Select;
const { Panel } = Collapse;
const { Title, Paragraph, Text, Link } = Typography;

var skillList=[...rawSkillList];
var color_map={
    '攻击': "red",
    '直伤': "volcano",
    '多刀': "orange",
    '回复':"cyan",
    '控制': "purple",
    '辅助': "blue",
    '距离':"lime",
    '过牌':"gold",
    '阻爆':"purple"
  };

var tagFilter=[];
var tagSelector=[];
for (var key in color_map){
    let element={text:<Tag color={color_map[key]}>{key} </Tag>, value:key};
    tagSelector.push(<Tag color={color_map[key]}>{key} </Tag>);
    tagFilter.push(element);}
    
var skillLists=[];
var recoverList=[];
var to_delete=[];
var skillsOnAttention=['放逐','镇骨'];
skillLists.push(skillList.filter(item => item['阶段']===0).sort(function(a,b){return a['技能'].localeCompare(b['技能'])}));
skillLists.push(skillList.filter(item => item['阶段']===1).sort(function(a,b){return a['技能'].localeCompare(b['技能'])}));
skillLists.push(skillList.filter(item => item['阶段']===2).sort(function(a,b){return a['技能'].localeCompare(b['技能'])}));

function update_to_delete(values){
    to_delete=[];
    for (let i=0;i<values.length;i++){
        for (let j=0;j<wujiangList[values[i]]['技能'].length;j++){
            to_delete.push(wujiangList[values[i]]['技能'][j]);
        }
    }
}
class App extends React.Component{
    constructor(){
        super();
        this.state={
            'skillList0': skillLists[0],
            'skillList0Length':skillLists[0].length,
            'skillList1': skillLists[1],
            'skillList1Length':skillLists[1].length,
            'skillList2': skillLists[2],
            'skillList2Length':skillLists[2].length,
            'skillsOnAttention':skillsOnAttention
        }
        this.del_skill=this.del_skill.bind(this);
        this.upd_state=this.upd_state.bind(this);
        this.resetList=this.resetList.bind(this);
        this.recover=this.recover.bind(this);
        this.resetPool=this.resetPool.bind(this);
        this.add_skill_onattention=this.add_skill_onattention.bind(this);
        this.columns0 = [
            {
              title:  () => { 
                return <div className='my-table-header'>回合({this.state['skillList0'].length})</div>;
              },
              key: 'name',
              render: text => <div className='my-cell'>
                <Popover content={text['属性'].map((x)=>{return (<Tag color={color_map[x]}>{x}</Tag>)})}>{text['技能']}</Popover></div>,
              sorter: (a, b) => a['技能'].localeCompare(b['技能']),
              filterMultiple:false,
              filters: tagFilter,
              onFilter: (value, record) => {
                  let satisfied=record['属性'].indexOf(value) !== -1;
                  return(satisfied)},
              onCell:(record, rowIndex) => {
                return {
                  onClick: event => {this.del_skill(record,0)}, // 点击行 
                  onDoubleClick: event => {},
                };
              }
            }
            
          ];
          this.columns1 = [
            {
              title:  () => { 
                return <div className='my-table-header'>结束({this.state['skillList1'].length})</div>;
              },
              key: 'name',
              render: text => <div className='my-cell'>
                <Popover content={text['属性'].map((x)=>{return (<Tag color={color_map[x]}>{x}</Tag>)})}>{text['技能']}</Popover></div>,
              sorter: (a, b) => a['技能'].localeCompare(b['技能']),
              filterMultiple:false,
              filters: tagFilter,
              onFilter: (value, record) => {
                  let satisfied=record['属性'].indexOf(value) !== -1;
                  return(satisfied)},
              onCell:(record, rowIndex) => {
                return {
                  onClick: event => {this.del_skill(record,1)}, // 点击行 
                  onDoubleClick: event => {},
                };
              }
            }
            
          ];
          this.columns2 = [
            {
              title:  () => { 
                return <div className='my-table-header'>受伤({this.state['skillList2'].length})</div>;
              },
              key: 'name',
              render: text => <div className='my-cell'>
                <Popover content={text['属性'].map((x)=>{return (<Tag color={color_map[x]}>{x}</Tag>)})}>{text['技能']}</Popover></div>,
              sorter: (a, b) => a['技能'].localeCompare(b['技能']),
              filterMultiple:false,
              filters: tagFilter,
              onFilter: (value, record) => {
                  let satisfied=record['属性'].indexOf(value) !== -1;
                  return(satisfied)},
              onCell:(record, rowIndex) => {
                return {
                  onClick: event => {this.del_skill(record,2)}, // 点击行 
                  onDoubleClick: event => {},
                };
              }
            }
            
          ];
    }
    
    upd_state(){
        this.setState({'skillList0': skillLists[0],
        'skillList0Length':skillLists[0].length,
        'skillList1': skillLists[1],
        'skillList1Length':skillLists[1].length,
        'skillList2': skillLists[2],
        'skillList2Length':skillLists[2].length,});
    }
    del_skill(val, skillListID){
        skillLists[skillListID] = skillLists[skillListID].filter(function(x) { return x['技能'] !== val['技能'] });
        recoverList.push(val['技能']);
        this.upd_state();
    }
    add_skill_onattention(value){
        skillsOnAttention=[];
        for (let v in value){
            skillsOnAttention.push(value[v]);
        }
        this.setState({'skillsOnAttention':skillsOnAttention});

    }
    resetList(){
        skillLists=[];
        skillLists.push(skillList.filter(item => item['阶段']===0).sort(function(a,b){return a['技能'].localeCompare(b['技能'])}));
        skillLists.push(skillList.filter(item => item['阶段']===1).sort(function(a,b){return a['技能'].localeCompare(b['技能'])}));
        skillLists.push(skillList.filter(item => item['阶段']===2).sort(function(a,b){return a['技能'].localeCompare(b['技能'])}));
        this.upd_state();
    }
    recover(){
        if(recoverList.length>0){
            let t=skillList.filter(item=> item['技能']===recoverList[recoverList.length-1]);
            skillLists[t[0]['阶段']].push(t[0]);
            this.upd_state();
            recoverList.pop();
        }
    }

    resetPool(){
        skillList=[...rawSkillList];
        for(let i=0;i<to_delete.length;i++){
            skillList=skillList.filter(item => item['技能']!==to_delete[i]);
        }
        this.resetList();
    }
    render(){
        return(<Layout>
        <Header><div className='App-header'>许劭辅助工具</div></Header>
        <Content>
        <Collapse
                bordered={false}
                defaultActiveKey={['3']}
                expandIcon={({ isActive }) => <CaretRightOutlined rotate={isActive ? 90 : 0} />}
                className="site-collapse-custom-collapse"
                >
                <Panel header="标注技能" key="1" className="site-collapse-custom-panel">
                <Select
                    mode="multiple"
                    allowClear
                    style={{ width: '100%' }}
                    defaultValue={['放逐','镇骨']}
                    placeholder="选择要特别关注的技能..."
                    optionFilterProp="children"
                    filterOption={(input, option) =>
                        option.children.indexOf(input) >= 0
                        }
                    onChange={this.add_skill_onattention}
                    >
                    {skillList.map((x)=>{
                        
                        return(<Option value={x['技能']}>{x['技能']}</Option>)})}
                
            
                </Select>
                <Card size="small" style={{'width': 'fit-content'} }>
                {this.state['skillsOnAttention'].map((x)=>{
                        let rate=-1;
                        for (let i=0;i<skillLists.length;i++){
                            if(skillLists[i].filter(function(y){return (y['技能']===x)}).length>0){
                                rate=3.0/skillLists[i].length;
                            }
                        }
                        if(rate>0){
                        return (<Card.Grid size="small" style={{'width': 'fit-content'} } > <Statistic
                        title={x}
                        value={100*rate}
                        precision={2}
                        valueStyle={{ color: '#3f8600' }}
                        
                        suffix="%"
                      /></Card.Grid>);}
                        else{
                            return(<Card.Grid size="small" style={{'width': 'fit-content'} } ><Statistic
                            title={x}
                            value="已使用"
                            valueStyle={{ color: '#cf1322' }}
                          /></Card.Grid>);
                        }
                    })}
                </Card>
                </Panel>
                <Panel header="标注属性 (施工中)" key="2" className="site-collapse-custom-panel">
    
                {tagSelector}

                </Panel>
                <Panel header="技能池配置" key="3" className="site-collapse-custom-panel">
                <Select
                    mode="multiple"
                    allowClear
                    style={{ width: '100%' }}
                    defaultValue={[]}
                    placeholder="选择未拥有武将"
                    optionFilterProp="children"
                    onChange={update_to_delete}
                    filterOption={(input, option) =>
                        option.children.indexOf(input) >= 0
                        }
                    >
                    {wujiangList.map((x)=>{
                        return(<Option value={x['ID']}>{x['武将']}</Option>);
                    })}
                </Select>
                <Button type='primary' onClick={this.resetPool}>提交</Button>
                </Panel>
        </Collapse>
        
        
        <Layout className="my-table">{<Table dataSource={[...this.state['skillList0']]} columns={this.columns0}  pagination={false}></Table>}</Layout>
        <Layout className="my-table">{<Table dataSource={[...this.state['skillList1']]} columns={this.columns1}  pagination={false}></Table>}</Layout>
        <Layout className="my-table">{<Table dataSource={[...this.state['skillList2']]} columns={this.columns2}  pagination={false}></Table>}</Layout>
        <Layout className="my-table"><Affix offsetTop={120} onChange={(affixed) => console.log(affixed)}>
        <Button onClick={this.resetList}>重置表单</Button>
        <p><Button onClick={this.recover}>撤销</Button></p>
        <p>单击标记为已使用</p>
        </Affix></Layout>
        </Content>
        </Layout>)
    }
}

export default App;